import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { ConfirmDialogModule, ConfirmationService } from 'primeng/primeng';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { CusOperationHttpService } from '../../../../http/custom-operation/custom-operation.http.service'
import { UpdateContactList } from 'app/pages/tzb/custom/custom-marketing/custom-contact/contact-list/update-contact-list/bean/updateContactList.bean';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';

@Component({
  selector: 'app-update-contact-list',
  templateUrl: './update-contact-list.component.html',
  styleUrls: ['./update-contact-list.component.scss'],
  providers: [CusOperationHttpService, Commfunc, ConfirmationService]

})
export class UpdateContactListComponent implements OnInit {

  @Output() outValue = new EventEmitter();
  @Input('in-value') inValue: any;
  @Input('key-label') keyLabel: any;
  @Input('kind-do') kindDo: string = '';

  msgs: any = [];
  btnText: string;
  adopt: string;
  noadopt: string;
  arr: any;
  cars: any;
  updateContactList: any = new UpdateContactList();
  userform: FormGroup;
  requireInput: boolean;
  requireInputMes: boolean = false;

  constructor(
    public httpService: CusOperationHttpService,
    public fb: FormBuilder,
  ) {

    // 
    this.userform = fb.group({
      custId: ['', Validators.required],
      custName: ['', Validators.required],
      dealNo: [''],
      ownerUser: ['', Validators.required],
      comments: ['', Validators.required],
    })
  }

  ngOnInit() {

    for (let i in this.inValue) {
      this.updateContactList[i] = this.inValue[i];
    }
    
    // ()
    if (this.updateContactList.contactTheme == '02' ||
      this.updateContactList.contactTheme == '11' ||
      this.updateContactList.contactTheme == '12' ||
      this.updateContactList.contactTheme == '13' ||
      this.updateContactList.contactTheme == '14' ||
      this.updateContactList.contactTheme == '15' ||
      this.updateContactList.contactTheme == '16') {
      this.requireInput = true;
    } else {
      this.requireInput = false;
    }
  }
 //
 filed(event) {
  let param = {
    custNo: this.updateContactList.custId
}
  this.httpService.selectCustBriefInfo(param).subscribe(
    data => {
        if (data.returnCode.code == 'success') {
          if(this.updateContactList.custId==data.custNo){
            this.updateContactList.custName = data.custName;
          }
        } 
    }, error => {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '', detail: '' });
    })
}
  // 
  doSave() {

    // 
    for (const i in this.userform.controls) {
      this.userform.controls[i].markAsDirty();
    }
    for (const i in this.userform.controls) {
      if (!this.userform.controls[i].valid) {
        return;
      }
    }

    // -()
    if (this.requireInput) {
      if (this.updateContactList.dealNo == '' || this.updateContactList.dealNo == undefined || this.updateContactList.dealNo == null) {
        this.requireInputMes = true;
        return
      } else {
        this.requireInputMes = false;
      }
    }

    // 
    delete this.updateContactList.processResponse;
    delete this.updateContactList.processStatus;
    delete this.updateContactList.contactTheme;

    this.httpService.updateFailCustConArrangeMent(this.updateContactList).subscribe(data => {
      if (data.returnCode.code == 'success') {
        // 
        this.outValue.emit(false);
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '', detail: "" });
    })
  }

}
